.buildControl {
    display: flex;
    justify-content: space-between;
    align-content: center;
    margin: 5px 0;

}

.buildControl button {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0 5px;
    width: 80px;
    border: 1px solid #AA6817;
    cursor: pointer;
    outline: none;
}

.buildControl button:disabled {
    background-color: #AC9980;
    border: 1px solid #7E7365;
    color: #ccc;
    cursor: default;
}

.buildControl button:hover:disabled {
    background-color: #AC9980;
    color: #ccc;
    cursor: not-allowed;
}

.label {
    padding: 10px;
    font-weight: bold;
    width: 80px;
}

.buildControl .less {
    background-color: #D39950;
    color: white;
}

.buildControl .more {
    background-color: #8F5E1E;
    color: white;
}

.buildControl .less:hover .buildControl.less:active {
    background-color: #DAA972;
    color: white;
}

